@import '~assets/styles/h3Imports';

.root {
  align-items: center;
  cursor: pointer;
  display: flex;
  font-size: em(16);
  font-weight: 600;
  left: em(16);
  position: absolute;
  right: var(--spacing-m);
}

.placeholder {
  color: var(--gray-3, #606565);
}

.multi-value {
  white-space: nowrap;
}

.hidden-value {
  opacity: 0;
  overflow: hidden;
  position: absolute;
  visibility: hidden;
}
