@import '~assets/styles/h3Imports';

.root {
  --brand-secondary-border-color: var(
    --menu-item-separator,
    rgba(#323264, 0.16)
  );

  align-items: center;
  border-bottom: em(1) solid var(--brand-secondary-border-color);
  cursor: default;
  display: flex;
  height: em(56);
  margin: 0;
  padding: var(--spacing-xs, em(16));

  &:last-child {
    border-bottom-left-radius: var(--card-border-radius, 0.5em);
    border-bottom-right-radius: var(--card-border-radius, 0.5em);
    color: var(--accent-interactive, #284b9b);
    font-weight: 700;
    height: auto;
    padding: var(--spacing-xxs, em(8)) var(--spacing-xs, em(16));
  }

  @media (max-width: $max-tablet-size) {
    height: em(48);
  }

  &:hover {
    background-color: var(--interface-info-background, #f6f8fb);
  }

  &:global(.selected) {
    background-color: var(--interface-info-background, #f6f8fb);
  }

  &:global(.option-focused) {
    outline: var(--focus-color, #0082aa) solid em(2);
    outline-offset: var(--focus-offset, em(-2));
  }

  &:first-child {
    border-top-left-radius: var(--card-border-radius, 0.5em);
    border-top-right-radius: var(--card-border-radius, 0.5em);
  }
}
