@import '~assets/styles/h3Imports';

.root {
  position: relative;
  width: 100%;

  @media (max-width: #{$max-phone-size}) {
    width: 100%;
  }

  & :global(.menu-dropdown-enter) {
    opacity: 0;
  }

  & :global(.menu-dropdown-enter-active) {
    opacity: 1;
    transition: opacity 0.3s;
  }

  & :global(.menu-dropdown-exit) {
    opacity: 1;
  }

  & :global(.menu-dropdown-exit-active) {
    opacity: 0;
    transition: opacity 0.3s;
  }
}

.dropdown {
  border-width: 0;
  font-weight: 400;
  width: 100%;
}

.control {
  align-items: center;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.control-indicator {
  color: var(--accent-interactive, #284b9b);
  position: absolute;
  right: var(--spacing-xs);
}

.text-field-container {
  margin-left: em(2);
  width: 100%;
}

.combobox {
  --input-border-color: var(--input-rested-border-color, #606565);

  align-items: center;
  display: flex;
  height: em(48);
  width: 100%;

  &[data-input-control-element='input'] {
    outline: none;
    width: 100%;

    &:read-only {
      --input-background-color: var(--white, white);
      --input-outline-color: var(--input-rested-border-color, #606565);
    }

    &:hover {
      --input-outline-color: var(--input-hover-outline-color, #414646);

      border-color: transparent;
      box-shadow: 0 0 0 em(2) var(--input-outline-color);
      outline: none;
    }

    &:global(.focused) {
      --input-outline-color-focused: var(--accent-interactive, #284b9b);

      border-color: transparent;
      box-shadow: 0 0 0 em(2) var(--input-outline-color-focused);
      outline: none;
    }
  }
}

.input-container {
  align-items: center;
  background-color: var(--input-background-color, white);
  display: flex;
  position: relative;
}

.dropdown-menu {
  margin-top: em(8);
  position: absolute;
  width: 100%;
  z-index: 1;
}

.checkbox {
  appearance: none;
  background: var(--checkbox-input-background, white);
  border-color: var(
    --checkbox-input-border-color,
    var(--checkbox-default-input-border-color, #284b9b)
  );
  border-radius: em(4);
  border-style: solid;
  border-width: var(--checkbox-input-border-width, em(2));
  display: grid;
  grid-column: 1;
  grid-row: 1;
  height: var(--checkbox-input-height, var(--checkbox-input-width, em(24)));
  margin-right: em(16);
  width: var(--checkbox-input-width, em(24));

  &:focus-visible {
    outline: em(2) solid var(--checkbox-input-focus-outline, #0082aa);
    outline-offset: em(2);
  }

  &::after {
    content: '';
    display: block;
    height: 100%;
    width: 100%;
  }

  &[data-checked='true'] {
    background: var(--checkbox-default-input-background, #284b9b);

    &::after {
      background: var(
          --checkbox-input-checkmark-background-image,
          url('./assets/check-mark.svg')
        )
        50% 50% no-repeat;
    }
  }
}

.wellness-checks-list-option {
  & [data-checked='true'] + .option-label {
    font-weight: 600;
  }
}
